import Swiper from "swiper/bundle";
import "swiper/css/bundle";

const swiperPopularCategories = () => {
    let swiperOne = null; //  ,    Swiper

    const swiperPopularCategories = () => {
      //    Swiper
      const initSwiper = () => {
        swiperOne = new Swiper(".popular-categories__swiper", {
          slidesPerView: "4",
          spaceBetween: 20,
          grabCursor: true,
          updateOnWindowResize: true,
          watchSlidesProgress: true,
          navigation: {
            nextEl: ".popular-categories__swiper-button-next",
            prevEl: ".popular-categories__swiper-button-prev",
          },
        });
      };
    
      //    Swiper
      const destroySwiper = () => {
        if (swiperOne !== null && swiperOne.destroy) {
          swiperOne.destroy();
          swiperOne = null;
        }
      };
    
      //      
      if (window.innerWidth > 768) {
        initSwiper();
      }
    
      //    
      window.addEventListener("resize", () => {
        if (window.innerWidth > 768) {
          //    768  Swiper   ,  
          if (swiperOne === null) {
            initSwiper();
          }
        } else {
          //      768  Swiper  ,  
          destroySwiper();
        }
      });
    };
    
    //    Swiper
    swiperPopularCategories();
};

export default swiperPopularCategories;


